.App {
  text-align: center;
}
.banner {
  height: 400px;
}

.side-bar {
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 100;
  background-color: gainsboro;
  width: 100px;
  display: flex;
  flex-direction: column;
  padding: 5px;
  border-radius: 10px;
}

.watched {
  margin: 5px;
  border-radius: 10px;
}
